html,
body,
#root {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
}
body {
  background: #000000;
  padding: 0px;
  margin: 0px;
}

canvas {
  display: block;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
}
